export default {
  props: ["openTreaty"],
  data() {
    return {
      acountError: '',
      passwordError: ''
    };
  },
  methods: {
    closeLoginForm() {
      this.$emit('loginform', false);
    },
    userLogin() {
      const userName = document.forms['login-form']["userNameOrEmailAddress"].value;
      const password = document.forms['login-form']["password"].value;
      console.log(userName);
      console.log(password);
      if (userName == null || userName == '') {
        this.acountError = '账号不能为空';
      }
      if (password == null || password == '') {
        this.passwordError = '密码不能为空';
      }
      if (userName == '1' && password == '1') {
        const userMessage = {
          loginstatu: true,
          userName: '初明',
          userId: "01",
          userHeadPhotoSrc: '头像.jpg'
        };
        this.$emit("loginstatu", userMessage);
      }
    },
    toChangePassword() {
      this.$emit('logincomponent', "forgetPassword");
    },
    toLogon() {
      this.$emit('logincomponent', "logonAcount");
    },
    openUserTreaty(val) {
      if (val == 1) {
        this.$emit('treaty', 'userTreaty');
      } else if (val == 2) {
        this.$emit('treaty', 'privacyPolicy');
      }
    }
  }
};