import require from '@/utils/require';
export default {
  data() {
    return {
      email: '',
      password: ''
    };
  },
  methods: {
    closeLoginForm() {
      this.$emit('loginform', false);
    },
    //获取验证码
    async getCode() {
      const {
        data: res
      } = await require.post('/api/email', {
        params: {
          // userName:this.userName,
          // password:this.password,
          email: this.email
        }
      });
      console.log(res);
    },
    changePassword() {
      console.log('2');
    },
    toChangePassword() {
      this.$emit('logincomponent', "forgetPassword");
    },
    toLogin() {
      this.$emit('logincomponent', "loginform");
    },
    openUserTreaty(val) {
      if (val == 1) {
        this.$emit('treaty', 'userTreaty');
      } else if (val == 2) {
        this.$emit('treaty', 'privacyPolicy');
      }
    }
  }
};