import "core-js/modules/es.array.push.js";
import bus from '@/utils/eventBus';
export default {
  data() {
    return {
      sendContent: '',
      myImgScr: require('@/assets/image/head/头像03.jpg'),
      myId: '200',
      chatName: '初明的聊天室',
      //用于接受变量
      userMess: null,
      otherScr: [{
        id: '01',
