        content: '你好欢迎使用日月明-共享装备。'
      }]
    };
  },
  methods: {
    send() {
      const nowTime = new Date();
      const years = nowTime.getFullYear();
      const month = nowTime.getMonth() + 1;
      const day = nowTime.getDate();
      console.log(`${years}/${month}/${day}`);
      this.chatContent.push({
        id: this.myId,
        time: `${years}/${month}/${day}`,
        content: this.sendContent
      });
      this.sendContent = '';
    },
    closeChat() {
      console.log('1');
      this.$emit('chatStatu', false);
    }
  },
  created() {
    this.$forceUpdate();
    bus.$once('chatmess', val => {
      this.userMess = val;
      console.log('this.userMess', this.userMess);
      this.chatName = this.userMess.name;
      this.chatContent = [{
        id: this.userMess.id,
        time: '2023/1/21',
        content: this.userMess.content
      }];

      //   (this.userimg = val.img.split('/'))
