import Vue from "vue";
import VueRouter from "vue-router";

import index from '../views/index'
import rent from '../views/rent.vue'
import map from '../views/map.vue'
import toolIntroduce from '../views/toolintroduce.vue'
import community from '../views/community'
import customerService from '../views/customerService'
import lawIntroduce from '../views/lawIntroduce'
import companyIntroduce from '../views/companyIntroduce'
import userCenter from '../views/userCenter'
//userCenter子组件
import userControl from '../components/userControl'
import order from '../components/order'
import message from '../components/message'
// import comment from '../components/comment'
import refund from '../components/refund'
// use 给vue装插件
Vue.use(VueRouter)
const router = new VueRouter({
  routes:[
    //重定向的路由规则
    {path:'/',redirect:'/index'},
    {path:'/index',component:index},
    {path:'/rent',component:rent},
    {path:'/map',component:map},
    {path:'/toolsIntroduce',component:toolIntroduce},
    {path:'/community',component:community},
    {path:'/lawIntroduce',component:lawIntroduce},
    {path:'/customerService',component:customerService},
    {path:'/companyIntroduce',component:companyIntroduce},

    {path:'/user-center',component:userCenter,props:true,
      children:[
        {path:'uesrControl',component:userControl},
        {path:'order',component:order},
        {path:'message',component:message},
        // {path:'comment',component:comment},
        {path:'refund',component:refund}
      ]
      //   
      // }
    },
  ]
})

export default router