export default {
  data() {
    return {
      maxDate: new Date(2023, 11, 31)
    };
  },
  setup() {
    const formatter = day => {
      const month = day.date.getMonth() + 1;
      const date = day.date.getDate();
      if (month === 5) {
        if (date === 1) {
          day.topInfo = '劳动节';
        } else if (date === 4) {
          day.topInfo = '青年节';
        }
      }
      return day;
    };
    return {
      formatter
    };
  }
};